:root {
    --col-main-blue: #01a6e8;
    --col-main-blue-hover: #0993ca;
    --col-main-dark-blue: #054c68;
    --col-main-orange: #ffac14;
    --col-main-orange-hover: #ec9800;
    --col-main-orange-hover: #9d6600;
    --col-white: #edeaea;
    --col-black: #111111;
    --col-dark-theme: #1a1a1a;
    --col-light-grey: #d3d3d3;
    --col-dark-grey: #aaa9a9;
    --col-light-theme-main: #f4f4f6;
    --col-light-theme-alt: #f3f3f7;
}

.container {
    width: min(100% - 30px, 1280px);
    margin-inline: auto;
}

.flex {
    display: flex;
}

.center {
    height: 100%;
    display: grid;
    place-items: center;
    justify-content: center;
    align-items: center;
}

body{
    background-color: var(--col-light-theme-alt);
}

@keyframes fadeIn {
    from {
      opacity: 0;
    }
    to {
      opacity: 1;
    }
  }

section {
    margin-top: 20px;
  }
  
  header {
    height: 120px;
    background-color: #333;
  }
  
  header h1 {
    line-height: 56px;
    font-size: 45px;
    color: var(--col-white);
    line-height: 120px;
  }

  footer {
    background-color: #333;
    color: var(--col-dark-grey);
    padding: 20px;
    text-align: center;
    position: fixed;
    bottom: 0;
    width: 100%;
  }
  
  .flex {
    display: flex;
  }
  
  .justify-between {
    justify-content: space-between;
  }
  
  .imgBox{
    display: flex;
    justify-content: center;
    gap: 40px;
    height: 470px;
    margin: 20px ;
  }

  .galleryBox{
    width: 100%;
    margin: 20px 0;

  }

  .banner{
    height: 200px;
    background-color: #fff;
  }

  h2 {
    font-size: 40px;
    font-weight: 500;
  }

  .BanText {
    font-size: 20px;
    line-height: 30px;
    margin: 10px 0;
  }

  .Btn {
    width: 100px;
    height: 40px;
    border-radius: 50px;
    margin: 0px 10px;
    border: 2px solid var(--col-main-blue);
    font-size: 15px;
    font-weight: 500;
    margin-bottom: 25px;
  }

  .primeBtn {
    background-color: var(--col-main-blue);
    color: #fff;
  }

  .secondBtn {
    background-color: #fff;
    color: var(--col-main-blue);
  }